@use 'base/variables' as v;
@use 'base/mixins' as m;
@use 'sass:color';

.reestablecer {
    margin-top: 5rem;
    .uptask {
        @include m.textoDegradado(v.$azul);
    }

    input[type='submit'] {
        @include m.boton(v.$azul, v.$blanco);
    }

    .acciones {
        a {
            color: v.$azul;
            &:hover {
                color: color.adjust(v.$azul, $lightness: -10% );
            }
        }
    }
}