@use 'base/variables' as v;
@use 'base/mixins' as m;
@use 'sass:color';

.login {
    margin-top: 5rem;
    .uptask {
        @include m.textoDegradado(v.$cyan);
    }

    input[type='submit'] {
        @include m.boton(v.$cyan, v.$blanco);
    }

    .acciones {
        a {
            color: v.$cyan;
            &:hover {
                color: color.adjust(v.$cyan, $lightness: -10% );
            }
        }
    }
}