@use 'base/variables' as v;
@use 'base/mixins' as m;
@use 'sass:color';

.confirmar {
    margin-top: 5rem;
    .uptask {
        @include m.textoDegradado(v.$indigo);
    }

    .acciones {
        justify-content: center;
        a {
            color: v.$indigo;
            &:hover {
                color: color.adjust(v.$indigo, $lightness: -10% );
            }
        }
    }
}